import React from 'react';
import bannerpunto from '../assets/img/PUNTO/punto.jpg';
import pic4 from '../assets/img/PUNTO/1.png';
import pic2 from '../assets/img/PUNTO/2.png';
import pic1 from '../assets/img/PUNTO/3.jpg';
import pic3 from '../assets/img/PUNTO/9.jpg';
import pic5 from '../assets/img/PUNTO/vertical.jpg';
import pic6 from '../assets/img/PUNTO/vertical1.jpg';
function Punto() {

return (
    <div className="fo" > 
      <div className="overlayt banim" id="bannerserv">  
        <img className="img-fluid" src={bannerpunto} alt="bannerPuntoLight" />
      </div>
    
    <div className="textwrap">
    <h1>Punto Light</h1>
    <p>Tiendas especializadas en iluminación llegaron a dar luz y brillo a Gallo amarillo. El reto para la agencia fue
    construir la comunicación de ambas marcas dando una personalidad diferente a cada una de ellas, preservando la misma
     esencia. El objetivo, incrementar el tráfico dentro de los puntos de venta localizados en la Zona Metropolitana de 
     Guadalajara, además de la generación de prospectos a nivel nacional, enfocándonos principalmente al sector de 
     arquitectura y construcción.</p>
    </div>

    <div className="pics-wrapper2">  
          <div className="row"> 
          	<div className="column2">
	              <div className="img-hover-zoom ">
	              		<img className="img-fluid border2" src={pic6} alt="PuntoLight Colores" /> 
	              </div>
	          </div>
              <div className="column2">
	              <div className="img-hover-zoom"> 
	              		<img className="img-fluid border" src={pic1} alt="PuntoLight Diseño" />        
	              </div>
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border" src={pic2} alt="PuntoLight Elegancia" />  
	              </div>
	          </div>
              <div className="column2">
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border" src={pic3} alt="PuntoLight Combinaciones" /> 
	              </div>
	              <div className="img-hover-zoom"> 
	              		<img className="img-fluid border" src={pic4} alt="PuntoLight Proyecto" />
	              </div>
	          </div>
	          <div className="column2">
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border2 " src={pic5} alt="PuntoLight iluminación" /> 
	              </div>
	          </div>
	          
                         
          </div>
            
    </div>
    
    </div>


  );
}
  
export default Punto;